import { Typography, List, ListItemText } from '@mui/material';
import { CodeBlock } from 'react-code-blocks';

import lifecycle from '../assets/lifecycle.jpeg';

const LifeCycle = () => {
    return (
        <div>
            <Typography variant="h4" paragraph>
                LifeCycle (useEffect)
            </Typography>
            <Typography paragraph>
                <b>" "</b>  React -   ,   
                ,      (, ,  
                ).
            </Typography>
            <Typography paragraph>
                    ,    , ,
                   ,       ,
                    .
            </Typography>
            <Typography variant="h5" paragraph>
                    React     :
            </Typography>
            <Typography variant="h6" paragraph component="div">
                  (Mounting Methods):
                <List sx={{ margin: '0 20px', listStyle: 'disc' }}>
                    <ListItemText sx={{ display: 'list-item' }}>constructor()</ListItemText>
                    <ListItemText sx={{ display: 'list-item', textDecoration: 'line-through' }}>
                        static getDerivedStateFromProps()
                    </ListItemText>
                    <ListItemText sx={{ display: 'list-item' }}>render()</ListItemText>
                    <ListItemText sx={{ display: 'list-item' }}>componentDidMount()</ListItemText>
                </List>
            </Typography>
            <Typography variant="h6" paragraph component="div">
                  (Updating Methods):
                <List sx={{ margin: '0 20px', listStyle: 'disc' }}>
                    <ListItemText sx={{ display: 'list-item' }}>
                        shouldComponentUpdate()
                    </ListItemText>
                    <ListItemText sx={{ display: 'list-item' }}>render()</ListItemText>
                    <ListItemText sx={{ display: 'list-item', textDecoration: 'line-through' }}>
                        getSnapshotBeforeUpdate()
                    </ListItemText>
                    <ListItemText sx={{ display: 'list-item' }}>componentDidUpdate()</ListItemText>
                </List>
            </Typography>
            <Typography variant="h6" paragraph component="div">
                  (Unmounting Methods):
                <List sx={{ margin: '0 20px', listStyle: 'disc' }}>
                    <ListItemText sx={{ display: 'list-item' }}>
                        componentWillUnmount()
                    </ListItemText>
                </List>
            </Typography>
            <Typography variant="h6" paragraph component="div">
                   (Error Handling Methods):
                <List sx={{ margin: '0 20px', listStyle: 'disc' }}>
                    <ListItemText sx={{ display: 'list-item' }}>componentDidCatch()</ListItemText>
                </List>
            </Typography>

            <img src={lifecycle} alt="Lifecycle" height={500} />

            <Typography variant="h5" paragraph>
                   :
            </Typography>
            <Typography variant="h6" paragraph component="div">
                Mounting ():
                <List sx={{ margin: '0 20px', listStyle: 'disc' }}>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>constructor(): </b>    .
                              .
                    </ListItemText>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>render(): </b>    React .
                    </ListItemText>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>componentDidMount(): </b>     
                          DOM.      , 
                             .
                    </ListItemText>
                </List>
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`class MyComponent extends React.Component {
    constructor (props) {
        super(props)
        this.state = { count: 0 }
        this.increment = this.increment.bind(this)
    }

    increment () {
        this.setState({ count: this.state.count + 1 })
    }

    componentDidMount () {
        //      .
        console.log('  !')
    }

    render () {
        return (
            <div>
                <p>: {this.state.count}</p>
                <button onClick={this.increment}>+++</button>
            </div>
        )
    }
}`}
                />
            </Typography>
            <Typography variant="h6" paragraph component="div">
                Updating ():
                <List sx={{ margin: '0 20px', listStyle: 'disc' }}>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>shouldComponentUpdate(nextProps, nextState): </b> 
                         .     
                          false,    .
                    </ListItemText>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>render(): </b>    React .
                    </ListItemText>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>componentDidUpdate(prevProps, prevState): </b>  
                           .    
                           , ,    
                            .
                    </ListItemText>
                </List>
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`class MyComponent extends React.Component {
    shouldComponentUpdate (nextProps, nextState) {
        //           name.
        return nextProps.name !== this.props.name
    }
    
    componentDidUpdate (prevProps, prevState) {
        //       .
        console.log('  !')
    }
    
    render () {
        return <div>Hello, {this.props.name}!</div>
    }
}`}
                />
            </Typography>
            <Typography variant="h6" paragraph component="div">
                Unmounting ():
                <List sx={{ margin: '0 20px', listStyle: 'disc' }}>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>componentWillUnmount(): </b>     DOM.
                            ,    
                         .
                    </ListItemText>
                </List>
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`import React from 'react'

class WillUnmountExample extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            time: 0
        }
    }

    componentDidMount () {
        this.timerID = setInterval(() => {
            this.setState(prevState => ({
                time: prevState.time + 1
            }));
        }, 1000);
    }

    componentWillUnmount () {
        clearInterval(this.timerID);
        alert('   DOM');
    }

    render () {
        return (
            <div>
                <p>: {this.state.time} </p>
            </div>
        )
    }
}

export default WillUnmountExample;`}
                />
            </Typography>
            <Typography variant="h6" paragraph component="div">
                Error Handling ( ):
                <List sx={{ margin: '0 20px', listStyle: 'disc' }}>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>componentDidCatch(error, info): </b>    
                         .      .
                    </ListItemText>
                </List>
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`import React, { Component } from 'react';

class ErrorBoundaryExample extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hasError: false,
            error: null,
            errorInfo: null
        };
    }

    componentDidCatch(error, errorInfo) {
        this.setState({
            hasError: true,
            error: error,
            errorInfo: errorInfo
        });
    }

    render() {
        if (this.state.hasError) {
            return (
                <div>
                    <h2>-   .</h2>
                    <p>{this.state.error && this.state.error.toString()}</p>
                    <p> : {this.state.errorInfo && this.state.errorInfo.componentStack}</p>
                </div>
            );
        }

        return this.props.children;
    }
}

class App extends Component {
    render() {
        return (
            <div>
                <h1>  ErrorBoundary</h1>
                <ErrorBoundaryExample>
                    {/*      */}
                    <ComponentWithError />
                </ErrorBoundaryExample>
            </div>
        );
    }
}

const ComponentWithError = () => {
    const [throwError, setThrowError] = useState(false);

    const handleClick = () => setThrowError(presState => !presState.throwError);

    if (throwError) {
        //  
        throw new Error('    ComponentWithError');
    }

    return (
        <div>
            <h3>   </h3>
            <button onClick={() => handleClick()}> </button>
        </div>
    )
}

export default App;`}
                />
            </Typography>
            <Typography variant="h5" paragraph>
                   :
            </Typography>
            <Typography paragraph>
                <b>useEffect</b> -    React,      
                   .
            </Typography>
            <Typography paragraph>
                <b> : , </b>     
                    .
            </Typography>
            <Typography paragraph>
                <b> :   </b> ,    
                 <b>useEffect</b>.   , <b>useEffect</b>  
                   .      , 
                <b> useEffect</b>    ,    .
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`//   componentDidMount
useEffect(() => { console.log("componentDidMount") }, []) 

//   componentDidUpdate
useEffect(() => { console.log("componentDidUpdate") }, [dependency]) 

//   componentDidMount  componentDidUpdate
useEffect(() => { console.log("componentDidMount  componentDidUpdate") }) 

    //   componentWillUnmount
useEffect(() => {
    return () => console.log('componentWillUnmount')
}, []);`}
                />
            </Typography>
            <Typography variant="h6" paragraph component="div">
                1. Mounting ():
                <List sx={{ margin: '0 20px', listStyle: 'disc' }}>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>{`useEffect(() => {}, [])`}: </b> <b>componentDidMount()</b>.
                               . 
                           [],    <b>useEffect</b> 
                        <b> componentDidMount</b>.
                    </ListItemText>
                </List>
            </Typography>
            <Typography variant="h6" paragraph component="div">
                2. Updating ():
                <List sx={{ margin: '0 20px', listStyle: 'disc' }}>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>{`useEffect(() => {}, [dependency])`}: </b>
                        <b> componentDidUpdate().</b>
                                
                         props  state.
                    </ListItemText>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>{`useEffect(() => {})`}: </b>    
                        .   <b>componentDidMount()</b> 
                        <b> componentDidUpdate()</b>.
                    </ListItemText>
                </List>
            </Typography>
            <Typography variant="h6" paragraph component="div">
                3. Unmounting ():
                <List sx={{ margin: '0 20px', listStyle: 'disc' }}>
                    <ListItemText sx={{ display: 'list-item' }}>
                        <b>{`useEffect(() => () => {})`}: </b>
                        <b> componentWillUnmount()</b>.      
                           (  DOM) .
                    </ListItemText>
                </List>
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`import React, { useState, useEffect } from 'react';

const Example = () => {
    const [count, setCount] = useState(0);

    useEffect(() => {
        console.log('  ');
        return () => {
            console.log('  ');
        };
    }, []); //      , 
            //        

    return (
        <div>
            <p>: {count}</p>
            <button onClick={() => setCount(count + 1)}></button>
        </div>
    );
}

export default Example;`}
                />
            </Typography>
            <Typography paragraph>
                   <b>useEffect</b>      
                ,     .    
                <b> useEffect</b> -     [],     
                 .
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`import React, { useState, useEffect } from 'react';

const Example = () => {
    const [count, setCount] = useState(0);
    const [message, setMessage] = useState('');

    useEffect(() => {
        setMessage(\`  : \${count}\`);
    }, [count]); //  [count] -     count

    return (
        <div>
            <p>{message}</p>
            <p>: {count}</p>
            <button onClick={() => setCount(count + 1)}></button>
        </div>
    );
}

export default Example;`}
                />
            </Typography>
            <Typography paragraph>
                   <b>useEffect</b>     count,  
                count  ,    [count].  count ,
                <b> useEffect</b>   message,     .
            </Typography>
        </div>
    );
};

export default LifeCycle;
