import React from 'react';
import { Typography } from '@mui/material';
import { CodeBlock } from 'react-code-blocks';

const Props = () => {
    return (
        <div>
            <Typography variant="h4" paragraph>
                Props
            </Typography>
            <Typography paragraph>
                 (  properties) -  ,    
                    React       .
            </Typography>
            <Typography paragraph variant="h5">
                  
            </Typography>
            <Typography paragraph>
                Props      .    
                    ,      . Props
                      props.      
                 .  , props      ,
                 , , ,    (   ).
            </Typography>
            <Typography paragraph>
                    -        
                ,    .    (immutable), 
                  .       , 
                  (state).
            </Typography>
            <Typography paragraph variant="h6">
                     :
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`//  
const ParentComponent = () => {
    const object = { name: "Sasha", age: 28 }
    const array = [1, 4, 2, 5];
    const subSecondComponent = () => <h4>SubSecondComponent</h4>;
    
    return <ChildComponent numberProps={5} name={"Masha"} isOnline={true} function={subSecondComponent} obj={object} arr={array} />;
}

//  
const ChildComponent = ({ numberProps, name, isOnline, subSecondComponent, obj, arr }) => { //    ,  props   
    return (
            <div> 
                <div>: {numberProps}</div>
                <span>{name}</span>
                {isOnline && <span>   </span>}
                {subSecondComponent()}
                <p>
                    {obj.name}, {obj.age} 
                </p>
                <div>{arr}</div>
                {arr && arr.map((item) => <div key={item}> : {item}</div>)}
            </div>
)}`}
                />
            </Typography>
            <Typography paragraph variant="h6">
                      (   
                this.props):
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`//  
const ParentComponent = () => {
    const object = { name: "Sasha", age: 28 }
    const array = [1, 4, 2, 5];
    const subSecondComponent = () => <h4>SubSecondComponent</h4>;
    
    return <ChildComponent numberProps={5} name={"Masha"} isOnline={true} function={subSecondComponent} obj={object} arr={array} />;
}

//ChildComponent.js
import React from 'react';

class ChildComponent extends React.Component{
  render(){
//         
    const { numberProps, name, isOnline, obj, arr, subSecondComponent } = this.props; 
    return (
        <div>
            <div>{numberProps}</div>
            <div>{name}</div>
            {isOnline && <div>Online</div>}
            {obj.name} <br />
            {obj.age} <br />
            {arr} <br />
            {subSecondComponent()}
        </div>
    );
  }
}

export default ChildComponent;`}
                />
            </Typography>
        </div>
    );
};

export default Props;
