import { Typography, Button, Input } from '@mui/material';
import { CodeBlock } from 'react-code-blocks';

const Events = () => {
    const handleClick = () => {
        alert('!');
    };

    const handleChange = (event) => {
        console.log(' :', event.target.value);
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        alert(' !');
    };

    const handleMouseOver = () => {
        console.log(' !');
    };

    const handleKeyDown = (event) => {
        console.log(' :', event.key);
    };

    return (
        <div>
            <Typography variant="h4" paragraph>
                Events
            </Typography>
            <Typography paragraph>
                 React  (events)      . 
                   ,   ,  ,
                   .
            </Typography>
            <Typography paragraph variant="h5">
                        :
            </Typography>
            <Typography paragraph variant="h6" component="div">
                1. onClick ()
                <Button
                    sx={{ ml: 1, color: 'black', border: '1px solid black' }}
                    onClick={handleClick}>
                     
                </Button>
            </Typography>
            <Typography paragraph>
                 onClick     .    
                   React.
            </Typography>
            <Typography paragraph sx={{ display: 'flex', justifyContent: 'space-between' }}>
                <CodeBlock
                    language={'js'}
                    text={`const MyComponent = () => {
    const handleClick = () => {
        alert('!');
    }

    return (
        <button onClick={handleClick}> </button>
    );
}`}
                />
            </Typography>
            <Typography paragraph variant="h6" component="div">
                2. onChange ():
                <Input
                    sx={{ ml: 1, color: 'black', border: '1px solid black' }}
                    onChange={handleChange}></Input>
            </Typography>
            <Typography paragraph>
                 onChange      ,  
                  (input),  (checkbox)     (radio).
            </Typography>
            <Typography paragraph>
                 React,  event  callback-   , 
                       .
                event.target.value    input .
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`const MyComponent = () => {
    const handleChange = (event) => {
        console.log(' :', event.target.value);
    }

    return (
        <input type="text" onChange={handleChange} />
    );
}`}
                />
            </Typography>
            <Typography paragraph variant="h6">
                3. onSubmit ( ):
            </Typography>
            <Typography paragraph>
                 onSubmit    .    ,
                      .
            </Typography>
            <Typography
                paragraph
                sx={{ display: 'flex', justifyContent: 'space-between' }}
                component="div">
                <CodeBlock
                    language={'js'}
                    text={`const MyComponent = () => {
    const handleSubmit = (event) => {
        event.preventDefault();
        alert(' !');
    }

    return (
        <form onSubmit={handleSubmit}>
            {/*    */}
            <label>: </label>
            <input />
            <br />
            <button type="submit"></button>
        </form>
    );
}`}
                />
                <form onSubmit={handleSubmit}>
                    <label>: </label>
                    <input />
                    <br />
                    <button type="submit"></button>
                </form>
            </Typography>

            <Typography paragraph variant="h6">
                4. onMouseOver ( ):
            </Typography>
            <Typography paragraph>
                 onMouseOver ,     .  
                       
                  .
            </Typography>
            <Typography
                paragraph
                sx={{ display: 'flex', justifyContent: 'space-between' }}
                component="div">
                <CodeBlock
                    language={'js'}
                    text={`const MyComponent = () => {
    const handleMouseOver = () => {
        console.log(' !');
    }
                        
    return (
        <div onMouseOver={handleMouseOver}>
               
        </div>
    );
}`}
                />
                <div onMouseOver={handleMouseOver}>   </div>
            </Typography>

            <Typography paragraph variant="h6">
                5. onKeyDown ( ):
            </Typography>
            <Typography paragraph>
                 onKeyDown      .  
                         
                 .
            </Typography>
            <Typography
                paragraph
                sx={{ display: 'flex', justifyContent: 'space-between' }}
                component="div">
                <CodeBlock
                    language={'js'}
                    text={`const MyComponent = () => {
    const handleKeyDown = (event) => {
        console.log(' :', event.key);
    }
                        
    return (
        <input type="text" onKeyDown={handleKeyDown} />
    );
}`}
                />
                <Input
                    sx={{ border: '1px solid black', height: '25px' }}
                    type="text"
                    onKeyDown={handleKeyDown}
                />
            </Typography>

            <Typography paragraph>
                   ,   onFocus, onBlur, onScroll,
                onKeyPress, onKeyUp  . .,       
                  .
            </Typography>
        </div>
    );
};

export default Events;
