import React from 'react';
import { Typography, Button } from '@mui/material';
import { CodeBlock } from 'react-code-blocks';

import ModeContext from '../contexts/ModeContext.js';

const Context = () => {
    const { toggleColorMode, mode } = React.useContext(ModeContext);

    return (
        <div>
            <Typography variant="h4" paragraph>
                Context (useContext)
            </Typography>
            <Typography paragraph>
                Context        
                 ,       
                .
            </Typography>
            <Typography paragraph>
                Context   props drilling,      
                   ,    . - 
                     .     
                   ,       .
            </Typography>
            <Typography paragraph variant="h5" component="div">
                 Context (      ):
                <Button sx={{ ml: 1 }} onClick={toggleColorMode}>
                      ( : {mode})
                </Button>
            </Typography>
            <Typography paragraph variant="h6">
                 1:  
            </Typography>
            <Typography paragraph>
                        `createContext()`  
                react.          `createContext()` 
                    (    
                default   ):
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`//contexts/ModeContext.js 
import React from 'react'; 
const ModeContext = React.createContext({ toggleColorMode: () => {} }); 
export default ModeContext;`}
                />
            </Typography>

            <Typography paragraph variant="h6">
                 2:   
            </Typography>
            <Typography paragraph variant="caption">
                *  ,       MUI, , 
                    .     .
            </Typography>
            <Typography paragraph>
                       -.
                -   ,     .
            </Typography>
            <Typography paragraph variant="subtitle1">
                 1.     
            </Typography>
            <Typography paragraph>
                    ModeContext.Provider   
                .  value     , 
                   .
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`// App.js
import React from 'react';
import ModeContext from './contexts/ModeContext.js';
import ComponentA from './ComponentA';

const App = () => {
    const [mode, setMode] = React.useState('light');
    const toggleColorMode = () => {
        setMode((prevMode) => (prevMode === 'light' ? 'dark' : 'light'));
    };

    return (
        <ModeContext.Provider value={{toggleColorMode, mode}}>
            <ComponentA />
        </ModeContext.Provider>
    );
}

export default App;`}
                />
            </Typography>

            <Typography paragraph variant="subtitle1">
                 2.     
            </Typography>
            <Typography paragraph>
                      ,    
                providers,       .  :
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`// providers/ModeProvider.js
import React from 'react';
import ModeContext from '../contexts/ModeContext.js';

const ModeProvider = ({ children }) => {
    const [mode, setMode] = React.useState('light');
    const toggleColorMode = () => {
        setMode((prevMode) => (prevMode === 'light' ? 'dark' : 'light'));
    };

    return (
        <ModeContext.Provider value={{toggleColorMode, mode}}>
                {children}
        </ModeContext.Provider>
    )
}

export default ModeProvider;

// App.js
import React from 'react';
import ModeProvider from './providers/ModeProvider.js';
import ComponentA from './ComponentA';

const App = () => {
    return (
        <ModeProvider>
            <ComponentA />
        </ModeProvider>
    );
}

export default App;`}
                />
            </Typography>

            <Typography paragraph variant="h6">
                 3:   useContext
            </Typography>
            <Typography paragraph>
                    useContext   , 
                    .     useContext  react
                  ,   ModeContext.  useContext  
                 ,        
                 . :
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'jsx'}
                    text={`// ComponentA.jsx
import React, { useContext } from 'react';
import ModeContext from '../contexts/ModeContext.js';
import ComponentB from './ComponentB';

function ComponentA() {
  const {toggleColorMode, mode} = useContext(ModeContext);

  return (
    <div>
      <h2>Component A</h2>
      <p> : {mode}</p>
      <button onClick={toggleColorMode}> </button>
      <ComponentB />
    </div>
  );
}

export default ComponentA;`}
                />
            </Typography>

            <Typography paragraph variant="h6">
                 4:    (  )
            </Typography>
            <Typography paragraph>
                        ,   
                .    ModeContext.Consumer.   
                  -    (  
                ).
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'jsx'}
                    text={`// ComponentA.jsx
import React from 'react';
import ModeContext from '../contexts/ModeContext.js';
import ComponentB from './ComponentB';

function ComponentA() {
  return (
    <div>
      <h2>Component A</h2>
      <ModeContext.Consumer>
        {({ toggleColorMode, mode }) => (
            <p> : {mode}</p>
            <button onClick={toggleColorMode}> </button>
        )}
      </ModeContext.Consumer>
      <ComponentB />
    </div>
  );
}

export default ComponentA;`}
                />
            </Typography>
        </div>
    );
};

export default Context;
