import { Typography, List, ListItemText } from '@mui/material';
import { CodeBlock } from 'react-code-blocks';

const HOC = () => {
    return (
        <div>
            <Typography variant="h4" paragraph>
                HO (Higher-Order Components)
            </Typography>
            <Typography paragraph>
                Higher Order Components (HOC)     React, 
                     .    
                ,   ,   ,   props,   
                   .
            </Typography>
            <Typography paragraph variant="h5">
                  HOC?
            </Typography>
            <Typography paragraph>
                HOC     ,      
                   .
            </Typography>
            <Typography paragraph>
                    ,  , 
                        
                  .
            </Typography>
            <Typography paragraph variant="h5">
                ,   HOC
            </Typography>
            <Typography paragraph component="div">
                HOC      React-:
                <List sx={{ margin: '0 20px' }}>
                    <ListItemText>
                        1. <b> :</b> HOC    
                        ,       , 
                               .
                    </ListItemText>
                    <ListItemText>
                        2. <b>  props:</b> HOC   
                         props .  , ,   
                               
                         .
                    </ListItemText>
                    <ListItemText>
                        3. <b> :</b> HOC    
                           , ,  ,
                            ..
                    </ListItemText>
                </List>
            </Typography>
            <Typography paragraph variant="h5">
                  HOC?
            </Typography>
            <Typography paragraph>
                 HOC     ,    
                  .
            </Typography>
            <Typography paragraph component="div">
                 HOC -   :
                <List sx={{ margin: '0 20px' }}>
                    <ListItemText>
                        1.  ,      .
                    </ListItemText>
                    <ListItemText>
                        2.     ,   
                              .
                    </ListItemText>
                    <ListItemText>3.    .</ListItemText>
                </List>
            </Typography>
            <Typography paragraph>  :</Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`const withLogger = (WrappedComponent) => {
    return (props) => {
        console.log('Component rendered:', WrappedComponent.name);
        return <WrappedComponent {...props} />;
    };
};`}
                />
            </Typography>
            <Typography paragraph variant="h5">
                  props?
            </Typography>
            <Typography paragraph>
                Props     HOC     :
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`const withExtraProps = (WrappedComponent) => {
    return (props) => {
        const extraProps = { additionalProp: "I'm extra!" };
        return <WrappedComponent {...props} {...extraProps} />;
    };
};`}
                />
            </Typography>
            <Typography paragraph variant="h5">
                  HOC
            </Typography>
            <Typography paragraph variant="h6">
                1. 
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`import React, { useEffect } from 'react';

const withLogger = (WrappedComponent) => {
    return (props) => {
        useEffect(() => {
            console.log(Component WrappedComponent.name mounted.);
        }, []);

        return <WrappedComponent {...props} />;
    };
};`}
                />
            </Typography>
            <Typography paragraph variant="h6">
                2. 
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`const withAuth = (WrappedComponent) => {
    return (props) => {
        const isAuthenticated = checkAuthStatus(); // ,    .
        if (isAuthenticated) {
            return <WrappedComponent {...props} />;
        } else {
            return <p>Please log in to access this content.</p>;
        }
    };
};
    
const AuthenticatedComponent = withAuth(ProfileComponent);`}
                />
            </Typography>
            <Typography paragraph variant="h6">
                3. 
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`const withLocalization = (WrappedComponent) => {
    return (props) => {
        const locale = getCurrentLocale(); //   .
        const localizedContent = getLocalizedContent(locale);
        return <WrappedComponent {...props} localizedContent={localizedContent} />;
    };
};

const LocalizedProfile = withLocalization(ProfileComponent);`}
                />
            </Typography>
            <Typography paragraph variant="h6">
                4.  
            </Typography>
            <Typography paragraph>
                <CodeBlock
                    language={'js'}
                    text={`import React, { useState } from 'react';

// HOC
const withCounter = (WrappedComponent) => {
    return (props) => {
        const [count, setCount] = useState(0);

        const incrementCount = () => {
            setCount(count + 1);
        };

        return (
            <WrappedComponent
            count={count}
            incrementCount={incrementCount}
            {...props}
            />
        );
    };
};
                    
// ,    HOC
const Counter = ({ count, incrementCount }) => {
    return (
        <div>
            <p>Count: {count}</p>
            <button onClick={incrementCount}>Increment</button>
        </div>
    );
};

//  HOC  
const CounterWithHOC = withCounter(Counter);

// ,     HOC
const App = () => {
    return (
        <div>
            <h1>Counter App</h1>
            <CounterWithHOC />
        </div>
    );
};`}
                />
            </Typography>
            <Typography paragraph>
                   withCounter -     , 
                  WrappedComponent    ,  
                 count   incrementCount. Counter -  , 
                   count     . CounterWithHOC -
                 Counter,   withCounter.
            </Typography>
            <Typography paragraph>
                 ,    CounterWithHOC  App,     count 
                 incrementCount,   HOC.
            </Typography>
            <Typography paragraph variant="h5">
                
            </Typography>
            <Typography paragraph>
                Higher Order Components     
                   .     
                         React.
            </Typography>
        </div>
    );
};

export default HOC;
